import React from 'react';
import './SubHead.css';
import './MyNoteSub.css';


function MyNoteSub(props){
  let nc = props.noteContents;
  return (
    <>
    <div className="bg">
      <div>
        <h2>{props.nav}</h2>
        <p>안녕하세요. {props.nav}입니다.</p>
      </div>
      <div className="note-card wrap">
        <ul>
          {
            nc.map((a)=>{
              return(
              <li className="card" key={a.id}>
                <h1>{a.title}</h1>
                <p className="content">{a.content}</p>
                <p className="date">{a.date}</p>
              </li>
              )
            })
          }
        </ul>
      </div>
    </div>
    </>
  );
}

export default MyNoteSub;